import React from 'react';
import Container from './Container';
import TimelyBookButton from './TimelyBookButton';
import CallToAction from '../components/CallToAction';
import LazyLoad from 'react-lazy-load';

const HomeInfo = () => {
    return [
        <section key="0" className="section">
            <div className="columns is-centered is-mobile">
                <Container>
                    <h1 className="welcome-title has-text-black">welcome to Undo</h1>
                    <p className="is-size-3 has-workflow-spacing is-double">
                        At Undo, Skincare Center, Tattoo Removal and Day Med Spa, we want you to <b><span className="has-text-link">look and feel your best</span></b>. Our team
                        are experts here to provide a customized treatment plan to help you get there! Our service begins with a
                        personalized consultation to fully understand your objective, and to introduce an array of <b>innovative treatments </b>
                        and products to get the results you want and deserve. Let’s get started.
                    </p>

                    <div className="has-workflow-spacing is-double">
                        <TimelyBookButton />
                    </div>
                </Container>
            </div>
        </section>,

        <div key="1">
            <CallToAction text="At <b>Undo, Skincare Center</b>, we use only the best and most advanced technology to make the tattoo removal process easy and painless!" />,
        </div>,

        <LazyLoad key="2">
            <div>
                <section className="section  has-background-white-ter">
                    <div className="columns is-centered is-mobile">
                        <Container>
                            <div className="columns is-vcentered">
                                <div className="column">
                                    <figure className="image is-square">
                                        <img src="/images/for_her.jpg" alt="For her" />
                                    </figure>
                                </div>
                                <div className="column content is-large">
                                    <h5 className="subtitle is-3 has-text-weight-bold has-workflow-spacing info-padding-title has-text-centered">Treatments for her</h5>
                                    <p>
                                        Let us get your to those goals through services such as Skin Rejuvenation, Hair Removal and much much more. Click below to find out more!
                            </p>
                                    <div className="has-text-centered">
                                        <div className="button is-link" onClick={() => { window.location = "/med_spa#for_her" }}>Learn more</div>
                                    </div>
                                </div>
                            </div>

                            <div className="columns is-vcentered">
                                <div className="column content is-large">
                                    <h5 className="subtitle is-3 has-text-weight-bold has-workflow-spacing info-padding-title has-text-centered">Treatments for him</h5>
                                    <p>
                                        Gone are the days when medical spas were just for women. Our Men’s medical spa offers a wide range of treatments and products tailored towards today’s modern and sophisticated man.
                            </p>
                                    <div className="has-text-centered">
                                        <div className="button is-link" onClick={() => { window.location = "/med_spa#for_him" }}>Learn more</div>
                                    </div>
                                </div>
                                <div className="column">
                                    <figure className="image is-square">
                                        <img src="/images/for_him.jpg" alt="For him" />
                                    </figure>
                                </div>
                            </div>
                        </Container>
                    </div>
                </section>
            </div>
        </LazyLoad>
    ];
}

export default HomeInfo;
